<template>
  <div class="prepare">
    <head-top title="备课">
      <van-icon name="plus" class="head-right" @click="toEdit"></van-icon>
    </head-top>

    <course-ware ref="courseWare" v-if="activeIndex===0" :class-options="classOptions"></course-ware>
    <class-practise ref="classPractise" v-else-if="activeIndex===1" :class-options="classOptions"></class-practise>
    <group-discussion ref="groupDiscussion" v-else></group-discussion>

    <div class="footer">
      <van-tabbar v-model="activeIndex">
        <van-tabbar-item v-for="(item, index) in menus" :key="index">
          <i slot="icon" class=" iconfont" :class="item.icon"></i>
          <span>{{item.name}}</span>
        </van-tabbar-item>
      </van-tabbar>
    </div>
  </div>
</template>

<script>
import CourseWare from "./components/CourseWare";
import ClassPractise from "./components/ClassPractise";
import GroupDiscussion from "./components/GroupDiscussion";

export default {
  name: "prepare",
  components: { CourseWare, ClassPractise, GroupDiscussion },
  data () {
    return {
      subjectID: this.$store.getters.subjectID,
      classOptions: [],
      activeIndex: 0,
      menus: [
        { name: "课件", icon: "icon-kejian" },
        { name: "随堂练习", icon: "icon-edu-biji" },
        { name: "小组讨论", icon: "icon-taolun" }
      ]
    };
  },
  created () {
    this.searchClassData();
  },
  methods: {
    toEdit () {
      switch (this.activeIndex) {
        case 0:
          this.$refs["courseWare"].toEdit();
          break;
        case 1:
          this.$refs["classPractise"].toEdit();
          break;
        case 2:
          this.$refs["groupDiscussion"].toEdit();
          break;
      }
    },
    searchClassData () {
      this.classOptions = [];
      this.$mainHttp.class.dataList({
        courseID: this.subjectID
      }).then(res => {
        if (res.Result && res.Datas && res.Datas.length > 0) {
          res.Datas.sort(r => r["CreateTime"]).reverse();
          res.Datas.forEach(item => {
            this.classOptions.push({
              text: item.Key["ClassName"],
              value: item.Key["ClassID"]
            });
          });
        } else if (res.Info) {
          this.$toast(res.Info);
        } else {
        }
      }).catch(() => {
      });
    }
  }
}
</script>

<style lang="less" scoped>
.footer {
  /deep/ .van-tabbar-item--active {
    color: #00cbab;
  }
}
</style>
