<template>
  <div class="prepare_group_discussion">
    <!--    <div class="sub_nav">-->
    <!--      <span>小组讨论</span>-->
    <!--      <van-icon name="plus" @click="toEdit"></van-icon>-->
    <!--    </div>-->
    <van-pull-refresh v-model="isLoading" @refresh="getGroupDiscussionList" class="pull_fresh">
      <van-cell-group v-if="groupDiscussionList && groupDiscussionList.length>0">
        <van-cell class="cell_main" v-for="(item, index) in groupDiscussionList" :key="index" :title="item.title"
                  :label="item.createTime">
          <template #right-icon>
            <van-icon name="delete" class="drop_icon" @click.stop.prevent="toDrop(item)"></van-icon>
          </template>
        </van-cell>
      </van-cell-group>
      <r-lack-data v-else></r-lack-data>
    </van-pull-refresh>

    <van-popup v-model="editShow" position="bottom" closeable>
      <div class="sub_nav">
        <span>小组讨论编辑</span>
      </div>
      <van-field v-model="discussContent" rows="2" label="" type="textarea" maxlength="500"
                 placeholder="请输入课题" autofocus autosize show-word-limit
                 :rules="[{ required: true, message: '请填写讨论课题' }]">
      </van-field>
      <van-button type="info" style="width: 100%;" @click="toSubmit">确定</van-button>
    </van-popup>
  </div>
</template>

<script>
import { Dialog } from "vant";
import { formatDate } from "@/utils/formatData";

export default {
  name: "group_discussion",
  data () {
    return {
      subjectID: this.$store.getters.subjectID,
      classHourID: this.$store.getters.classHourID || "",
      classHour: this.$store.getters.classHour,

      groupDiscussionList: [],
      discussContent: "",
      editShow: false,
      isLoading: false
    };
  },
  mounted () {
    this.getGroupDiscussionList();
  },
  methods: {
    getGroupDiscussionList () {
      this.groupDiscussionList = [];
      this.isLoading = true;
      this.$mainHttp.prepareGroupSubject.dataList({
        CHID: this.classHourID
      }).then(res => {
        if (res.Result) {
          res.Datas.sort((a, b) => {
            return a["CreateTime"] > b["CreateTime"] ? -1 : 1;
          });

          res.Datas.forEach(item => {
            this.groupDiscussionList.push({
              title: item["PGSContent"],
              createTime: formatDate(item["CreateTime"], "yyyy-MM-dd"),
              other: item["PGSTime"],
              id: item["PGSID"]
            });
          })
        } else if (res.Info) {
          this.$toast(res.Info);
        }
        this.isLoading = false;
      }).catch(() => {
        this.isLoading = false;
      });
    },
    toEdit () {
      this.discussContent = "";
      this.editShow = true;
    },
    toDrop (item) {
      Dialog.confirm({
        message: "确认删除当前讨论课题？"
      }).then(() => {
        this.toDelete(item);
      }).catch(() => {
      });
    },
    toDelete (item) {
      this.$mainHttp.prepareGroupSubject.dataDelete({
        ids: item.id
      }).then(res => {
        if (res.Result) {
          this.getGroupDiscussionList();
          this.$toast("删除成功");
        } else if (res.Info) {
          this.$toast(res.Info);
        } else {
          this.$toast("删除失败");
        }
      }).catch(() => {
        this.$toast("操作失败");
      });
    },
    toSubmit () {
      if (!this.discussContent) {
        this.$toast.fail("请填写讨论课题 ");
        return false;
      }

      this.$mainHttp.prepareGroupSubject.dataAdd({
        pgsID: "",
        CHID: this.classHourID,
        pgsContent: this.discussContent,
        pgsTime: 0
      }).then(res => {
        if (res.Result) {
          this.$toast("保存成功");
          this.getGroupDiscussionList();
          this.editShow = false;
        } else if (res.Info) {
          this.$toast(res.Info);
        } else {
          this.$toast("保存失败");
        }
      }).catch(() => {
        this.$toast("操作失败");
      });
    }
  }
}
</script>
