<template>
  <div class="prepare_course_ware">
    <!--    <div class="sub_nav">-->
    <!--      <span>课件</span>-->
    <!--      <van-icon name="plus" @click="toEdit"></van-icon>-->
    <!--    </div>-->

    <van-tabs @click="onClick">
      <van-tab v-for="(item, index) in list" :key="index" :title="item.title">
        <van-pull-refresh v-model="isLoading" @refresh="getCourseWareList" class="pull_fresh">
          <van-cell-group v-if="item.courseWareList && item.courseWareList.length>0">
            <van-cell class="cell_main" v-for="(sub, k) in item.courseWareList" :key="k">
              <template #icon>
                <van-icon name="description" class="cell_icon"></van-icon>
              </template>
              <template #title>
                <div class="line_title">{{sub.name + '.' + sub.ext}}</div>
              </template>
              <template #label>
                <div class="line_title">
                  <span class="space">{{sub.createTime}} </span>
                  <van-tag plain type="success" v-show="sub.className" v-if="listSelectedIndex !== 1" class="space">{{sub.className}}</van-tag>
                  <span v-if="sub.size > 1024*1024">{{Math.round(sub.size / 1024.0 / 10.24 ) / 100}}MB</span>
                  <span v-else>{{Math.round(sub.size / 10.24 ) / 100}}KB</span>
                </div>
              </template>
              <template #right-icon>
                <van-icon name="delete" class="drop_icon" @click.stop.prevent="toDrop(sub)"></van-icon>
              </template>
            </van-cell>
          </van-cell-group>
          <r-lack-data v-else></r-lack-data>
        </van-pull-refresh>
      </van-tab>
    </van-tabs>

    <van-popup v-model="editShow" position="bottom" closeable style="height: 100%;">
      <div id="headTop">
        <span class="header_title">课件备课编辑</span>
        <span @click="editShow=false" class="head-back">
          <i class="iconfont icon-fanhui"></i>
        </span>
        <van-icon name="success" class="head-right" @click="toSubmit"></van-icon>
      </div>
      <div class="popup-main">
        <div v-if="listSelectedIndex!==1">
          <div class="sub_nav">
            备课班级
          </div>
          <van-field readonly clickable name="picker" :value="classSelectedName"
                     placeholder="点击选择班级" @click="showPicker = true">
          </van-field>
        </div>
        <div v-show="listSelectedIndex===1 || classSelectedValue">
          <div class="sub_nav">
            备课课件
          </div>
          <van-checkbox-group v-model="materialSelected" style="text-align: left;">
            <van-cell-group>
              <van-cell v-for="(item, index) in materialList" :key="index" :title="item.name + '.' + item.ext" clickable
                        @click="materialCheck(item)">
                <van-checkbox slot="right-icon" sharp="square" :name="item.materialID"></van-checkbox>
              </van-cell>
            </van-cell-group>
          </van-checkbox-group>
        </div>
      </div>
    </van-popup>

    <van-popup v-model="showPicker" position="bottom">
      <van-picker show-toolbar :columns="classOptions" @confirm="changeClass" @cancel="showPicker = false">
      </van-picker>
    </van-popup>
  </div>
</template>

<script>
import { Dialog } from "vant";
import { formatDate } from "@/utils/formatData";
import axios from "axios";

export default {
  name: "course-ware",
  props: {
    classOptions: {
      type: Array,
      default: () => {
        return [];
      }
    }
  },
  data () {
    return {
      subjectID: this.$store.getters.subjectID,
      classHourID: this.$store.getters.classHourID || "",
      classHour: this.$store.getters.classHour,

      list: [
        { title: "课前", attribute: 1, courseWareList: [] },
        { title: "课中", attribute: 2, courseWareList: [] },
        { title: "课后", attribute: 3, courseWareList: [] }
      ],
      listSelectedIndex: 0,

      editShow: false,
      classSelectedName: "",
      classSelectedValue: "",
      materialList: [],
      showPicker: false,
      materialSelected: [],
      oldMaterialSelected: [],
      isLoading: false
    };
  },
  mounted () {
    this.getMaterialList();
    this.getCourseWareList();
  },
  methods: {
    onClick (value) {
      this.listSelectedIndex = value;
      this.getCourseWareList();
    },
    getCourseWareList () {
      const selected = this.list[this.listSelectedIndex];
      selected.courseWareList = [];
      this.isLoading = true;
      this.$mainHttp.courseware.dataList({
        chID: this.classHourID,
        attribute: this.list[this.listSelectedIndex].attribute
      }).then(res => {
        if (res.Result) {
          res.Datas.sort((a, b) => {
            return a["CreateTime"] > b["CreateTime"] ? -1 : 1;
          });

          res.Datas.forEach(item => {
            selected.courseWareList.push({
              name: item["MaterialName"],
              createTime: formatDate(item.Key["CreateTime"], "yyyy-MM-dd"),
              ext: item["MaterialFileExt"],
              size: item["MaterialFileSize"],
              id: item.Key["CID"],
              materialID: item.Key["MaterialID"],
              classID: item["ClassID"],
              className: item["ClassName"]
            });
          })
        } else if (res.Info) {
          this.$toast(res.Info);
        }
        this.isLoading = false;
      }).catch(() => {
        this.isLoading = false;
      });
    },
    getMaterialList () {
      this.materialList = [];
      this.$mainHttp.courseware.materialList({
        token: this.$store.getters.token
      }).then(res => {
        if (res.Result && res.Datas && res.Datas.length > 0) {
          res.Datas.forEach(item => {
            this.materialList.push({
              name: item["MaterialName"],
              materialID: item["MaterialID"],
              size: item["MaterialFileSize"],
              ext: item["MaterialFileExt"]
            });
          });
        } else if (res.Info) {
          this.$toast(res.Info);
        } else {
        }
      }).catch(() => {
      });
    },
    toDrop (sub) {
      Dialog.confirm({
        message: "确认移除当前课件？"
      }).then(() => {
        this.toDelete(sub);
      }).catch(() => {
      });
    },
    toDelete (sub) {
      this.$mainHttp.courseware.dataDelete({
        ids: sub.id
      }).then(res => {
        if (res.Result) {
          this.$toast("移除成功");
          this.getCourseWareList();
        } else if (res.Info) {
          this.$toast(res.Info);
        } else {
          this.$toast("移除失败");
        }
      }).catch(() => {
        this.$toast("操作异常");
      });
    },
    toEdit () {
      this.editShow = true;
      this.classSelectedName = "";
      this.classSelectedValue = "";
      this.materialSelected = [];
      this.oldMaterialSelected = [];
      if (this.listSelectedIndex === 1) {
        this.materialSelected = this.list[1].courseWareList.map(r => r.materialID);
        this.oldMaterialSelected = this.materialSelected.slice(0, this.materialSelected.length);
      }
    },

    changeClass (selected) {
      this.classSelectedName = selected.text;
      this.classSelectedValue = selected.value;

      this.materialSelected = [];
      this.oldMaterialSelected = [];
      this.materialSelected = this.list[this.listSelectedIndex].courseWareList.filter(r => r.classID === this.classSelectedValue).map(r => r.materialID);
      this.oldMaterialSelected = this.materialSelected.slice(0, this.materialSelected.length);
      this.showPicker = false;
    },
    materialCheck (item) {
      const index = this.materialSelected.findIndex(r => r === item.materialID);
      if (index > -1) {
        this.materialSelected.splice(index, 1);
      } else {
        this.materialSelected.push(item.materialID);
      }
    },
    toSubmit () {
      if (this.listSelectedIndex !== 1 && !this.classSelectedValue) {
        this.$toast.fail("请先选择班级");
        return false;
      }
      const listAdd = this.materialSelected.filter(r => this.oldMaterialSelected.indexOf(r) === -1);
      const listDelete = this.oldMaterialSelected.filter(r => this.materialSelected.indexOf(r) === -1);
      let requestAll = [];
      // 是否增加
      if (listAdd.length > 0) {
        requestAll.push(
          this.$mainHttp.courseware.dataAdd({
            chID: this.classHourID,
            materialIDs: listAdd.join(","),
            attribute: this.list[this.listSelectedIndex].attribute,
            classID: this.classSelectedValue
          }));
      }

      if (listDelete.length > 0) {
        let ids = [];
        if (this.listSelectedIndex === 1) {
          ids = this.list[1].courseWareList.map(r => {
            if (listDelete.indexOf(r.materialID) > -1) return r.id;
          });
        } else {
          ids = this.list[this.listSelectedIndex].courseWareList.filter(r => r.classID === this.classSelectedValue).map(r => {
            if (listDelete.indexOf(r.materialID) > -1) return r.id;
          });
        }
        requestAll.push(
          this.$mainHttp.courseware.dataDelete({
            ids: ids.join(",")
          }));
      }

      if (requestAll.length > 0) {
        axios.all(requestAll).then(
          axios.spread((...resList) => {
            let failIndexs = [];
            resList.forEach((res, i) => {
              if (res.Result) {
              } else {
                failIndexs.push(i);
              }
            });

            if (failIndexs.length === 0) {
              this.$toast("操作成功");
              this.getCourseWareList();
              this.editShow = false;
            } else if (failIndexs.length === 1 && failIndexs.length < resList.length) {
              this.$toast("部分操作未保存成功，请重试");
              this.getCourseWareList();
              this.editShow = false;
            } else {
              this.$toast("操作失败！");
            }
          })
        );
      } else {
        this.editShow = false;
      }
    }
  }
}
</script>

<style lang="less" scoped>
@import "./../../../../../styles/common/base";
.prepare_course_ware {
  .cell_main {
    .space {
      margin-right: 2.6vw;
    }
  }

  .popup-main {
    margin-top: 44px;
  }

}
</style>
