<template>
  <div class="prepare_class_practise">
    <!--    <div class="sub_nav">-->
    <!--      <span>随堂练习</span>-->
    <!--      <van-icon name="plus" @click="toEdit"></van-icon>-->
    <!--    </div>-->

    <van-tabs @click="onClick">
      <van-tab v-for="(item, index) in list" :key="index" :title="item.title">
        <van-pull-refresh v-model="isLoading" @refresh="getPractiseList" class="pull_fresh">
          <van-cell-group v-if="item.practiseList && item.practiseList.length>0">
            <van-cell class="cell_main" v-for="(sub, k) in item.practiseList" :key="k">
              <template #icon>
                <van-icon name="description" class="cell_icon"></van-icon>
              </template>
              <template #title>
                <div class="line_title">{{sub.name}}</div>
              </template>
              <template #label>
                <div class="line_title">
                  <span class="space">{{sub.createTime}} </span>
                  <van-tag v-show="index !== 1" plain type="success" class="space">{{sub.className}}</van-tag>
                </div>
              </template>
              <template #right-icon>
                <van-icon name="delete" class="drop_icon" @click.stop.prevent="toDrop(sub)"></van-icon>
              </template>
            </van-cell>
          </van-cell-group>
          <r-lack-data v-else></r-lack-data>
        </van-pull-refresh>
      </van-tab>
    </van-tabs>

    <van-popup v-model="editShow" position="bottom" closeable style="height: 100%;">
      <div id="headTop">
        <span class="header_title">随堂练习备课编辑</span>
        <span @click="editShow=false" class="head-back">
          <i class="iconfont icon-fanhui"></i>
        </span>
        <van-icon name="success" class="head-right" @click="toSubmit"></van-icon>
      </div>
      <div class="popup-main">
        <div>
          <div class="sub_nav">
            题库选择
          </div>
          <van-field readonly clickable name="picker" :value="practiseSelectedName"
                     placeholder="点击选择题库" @click="showPicker = true">
          </van-field>
        </div>
        <div v-if="listSelectedIndex !==1">
          <div class="sub_nav">
            班级选择
          </div>
          <van-checkbox-group v-model="classSelected" style="text-align: left;">
            <van-cell-group>
              <van-cell v-for="(item, index) in classOptions" :key="index" :title="item.text" clickable
                        @click="classCheck(item)">
                <van-checkbox slot="right-icon" sharp="square" :name="item.value"></van-checkbox>
              </van-cell>
            </van-cell-group>
          </van-checkbox-group>
        </div>
      </div>
    </van-popup>

    <van-popup v-model="showPicker" position="bottom">
      <van-picker show-toolbar :columns="practiseOptions" @confirm="changePractise" @cancel="showPicker = false">
      </van-picker>
    </van-popup>
  </div>
</template>

<script>
import { Dialog } from "vant";
import { formatDate } from "@/utils/formatData";

export default {
  name: "class_practise",
  props: {
    classOptions: {
      type: Array,
      default: () => {
        return [];
      }
    }
  },
  data () {
    return {
      subjectID: this.$store.getters.subjectID,
      classHourID: this.$store.getters.classHourID || "",
      classHour: this.$store.getters.classHour,

      list: [
        { title: "课前", attribute: 1, practiseList: [] },
        { title: "课中", attribute: 2, practiseList: [] },
        { title: "课后", attribute: 3, practiseList: [] }
      ],
      listSelectedIndex: 0,

      editShow: false,
      selectedTaskID: "",
      classSelected: [],

      practiseOptions: [],
      practiseSelectedName: "",
      practiseSelectedValue: "",
      practiseLibraryList: [],
      showPicker: false,
      isLoading: false
    };
  },
  mounted () {
    this.getPractiseLibraryList();
    this.getPractiseList();
  },
  methods: {
    onClick (value) {
      this.listSelectedIndex = value;
      this.getPractiseList();
    },
    getPractiseList () {
      const selected = this.list[this.listSelectedIndex];
      selected.practiseList = [];
      this.isLoading = true;
      this.$mainHttp.classPractise.dataList({
        chID: this.classHourID,
        attribute: this.list[this.listSelectedIndex].attribute
      }).then(res => {
        if (res.Result) {
          res.Datas.sort((a, b) => {
            return a["CreateTime"] > b["CreateTime"] ? -1 : 1;
          });

          res.Datas.forEach(item => {
            selected.practiseList.push({
              name: item["Title"],
              createTime: formatDate(item.Datas["CreateTime"], "yyyy-MM-dd"),
              id: item.Datas["CPID"],
              taskId: item["CIPID"],
              libraryID: item.Datas["PLID"],
              classID: item["ClassID"].split(","),
              className: item["ClassName"]
            });
          })
        } else if (res.Info) {
          this.$toast(res.Info);
        }
        this.isLoading = false;
      }).catch(() => {
        this.isLoading = false;
      });
    },
    getPractiseLibraryList () {
      this.practiseLibraryList = [];
      this.$mainHttp.classPractise.LibraryList({
        token: this.$store.getters.token
      }).then(res => {
        if (res.Result && res.Datas && res.Datas.length > 0) {
          res.Datas.forEach(item => {
            this.practiseLibraryList.push({
              text: item["PLTitle"],
              value: item["PLID"],
              createTime: formatDate(item["CreateTime"], "yyyy-MM-dd")
            });
          });
        } else if (res.Info) {
          this.$toast(res.Info);
        } else {
        }
      }).catch(() => {
      });
    },

    toDrop (sub) {
      Dialog.confirm({
        message: "确认移除当前课件？"
      }).then(() => {
        this.toDelete(sub);
      }).catch(() => {
      });
    },
    toDelete (sub) {
      this.$mainHttp.classPractise.dataDelete({
        ids: sub.id
      }).then(res => {
        if (res.Result) {
          this.$toast("移除成功");
          this.getPractiseList();
        } else if (res.Info) {
          this.$toast(res.Info);
        } else {
          this.$toast("移除失败");
        }
      }).catch(() => {
        this.$toast("操作异常");
      });
    },
    toEdit () {
      this.editShow = true;
      this.classSelected = [];

      this.practiseOptions = this.practiseLibraryList.slice(0, this.practiseLibraryList.length);
      let selected = [];
      this.list.forEach(item => {
        item.practiseList.forEach(sub => {
          selected.push(sub.libraryID);
        });
      });

      this.practiseOptions.forEach(item => {
        if (selected.indexOf(item.value) > -1) {
          item.disabled = true;
        }
      });
    },
    changePractise (selected) {
      this.practiseSelectedName = selected.text;
      this.practiseSelectedValue = selected.value;
      this.showPicker = false;
    },
    classCheck (item) {
      const index = this.classSelected.findIndex(r => r === item.value);
      if (index > -1) {
        this.classSelected.splice(index, 1);
      } else {
        this.classSelected.push(item.value);
      }
    },
    toSubmit () {
      if (!this.practiseSelectedValue) {
        this.$toast.fail("请选择题库");
        return false;
      }
      if (this.listSelectedIndex !== 1 && this.classSelected.length === 0) {
        this.$toast.fail("请选择布置班级");
        return false;
      }

      let reqData = {
        cpID: this.selectedTaskID,
        chID: this.classHourID,
        plID: this.practiseSelectedValue,
        attribute: this.list[this.listSelectedIndex].attribute
      };
      if (this.listSelectedIndex !== 1) reqData.classID = this.classSelected.join(",")

      this.$mainHttp.classPractise.dataAdd(reqData).then(res => {
        if (res.Result) {
          this.$toast("保存成功");
          this.getPractiseList();
          this.editShow = false;
        } else if (res.Info) {
          this.$toast(res.Info);
        } else {
          this.$toast("保存失败");
        }
      }).catch(() => {
        this.$toast("操作失败");
      });
    }
  }
}
</script>

<style lang="less" scoped>
@import "./../../../../../styles/common/base";
.prepare_class_practise {
  .cell_main {
    .space {
      margin-right: 2.6vw;
    }
  }
  .popup-main {
    margin-top: 44px;
  }
}
</style>
